export default {
	data() {
		return {
			order: null,
			first: true //是否是第一次支付
		};
	},
	onLoad(opt) {
		const paramsObj = JSON.parse(opt.paramsStr)
		this.initData(paramsObj)
	},
	methods: {
		initData(order) {
			this.order = order
		},
		//减少数量
		minus() {
			if (this.order.amount === 1) {
				return
			} else {
				this.chanegAmount()
			}
		},
		//增加数量
		plus() {
			this.chanegAmount(1)
		},
		chanegAmount(flag) {
			let order = { ...this.order
			}
			let amount = order.amount
			if (flag) {
				amount++
			} else {
				amount--
			}
			let total = (amount * order.price).toFixed(1)
			this.order = {
				...order,
				amount,
				total
			}
		},
		//模拟支付
		payment() {
			//避免重复支付
			if (this.first) {
				let snackOrder = uni.getStorageSync('snackOrder') || []
				snackOrder.unshift(this.data.order)
				uni.setStorageSync('snackOrder', snackOrder)
				uni.showToast({
					title: '支付成功',
				})
				this.first=false
			} else {
				uni.showToast({
					title: '已支付',
					icon: 'none'
				})
			}
		}
	}
}
